import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-page-before-form',
  templateUrl: './page-before-form.component.html',
  styleUrls: ['./page-before-form.component.scss']
})
export class PageBeforeFormComponent implements OnInit {

  text: string;
  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.http
      .get('assets/text/page-before-form-text.txt', { responseType: 'text' })
      .subscribe(
        data =>
          (this.text = data)
      );
  }

}
